import React, { Component } from 'react'
import { Link } from 'react-router-dom'

const navList = [{
  name: '',
  to: '/'
}, {
  name: '',
  to: '/blog'
}]

const sayHi = [
  '',
  ',',
  '',
  '?',
  '!!',
  '',
  '?',
  '?',
  '',
  '',
  '...'
]

export default class DisscussHeader extends Component {
  constructor () {
    super()
    this.state = {
      greetWord: ''
    }
  }
  componentDidMount () {
    this.rdSayHi()
  }
  rdSayHi () {
    this.setState({ greetWord: sayHi[parseInt(Math.random() * 10, 10)] })
  }
  render() {
    const { greetWord } = this.state
    return (
      <div className="disscuss-header">
        <h1>{greetWord}</h1>
        <nav className="disscuss-nav">
          {
            navList.map((nav, i) => (
              <Link to={nav.to} key={i}>{nav.name}</Link>
            ))
          }
        </nav>
      </div>
    )
  }
}



